.ly-tab {
  position: relative;
  background-color: rgba(255 ,255, 255, .8);
  width: 100%;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #ddd;
}
.ly-tab.ly-tab-fix-bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  border-top: 1px solid #ccc;
  border-bottom: none;
}
.ly-tab.ly-tab-fix-bottom .ly-tab-item {
  border-bottom: none!important;
}
.ly-tab-list {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  padding: 10px;
  min-width: 100%;
}
.ly-tab-active-bar {
  position: absolute;
  bottom: 3px;
  left: 0;
  width: 30px;
  height: 3px;
  border-radius: 4px;
}
.ly-tab-item {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  font-size: 14px;
  text-align: center;
  padding: 0 5px;
}
.ly-tab-item:not(:first-child) {
  margin-left: 15px;
}

